/* Header */
.header {
  grid-area: header;

  background-color: #323232;
  height: 46px;
}
.header_container {
  align-items: center;
  flex-direction: row;
  justify-content: space-between; 
}
.header_social {
  display: flex;
  align-items: center;
}
.header_social a {
  display: block;
  width: 44px;
  height: 46px;
  border-left: 1px solid rgb(110, 110, 110);
  text-align: center;
  color:#ccc;
  text-decoration: none;
}
.header_social a:last-child {
  border-right: 1px solid rgb(110, 110, 110)
}
.top_widgets {
  display: flex;
}
.top_widgets p {
  padding-top: 10px;
  margin: 0;
  display: block;
  width: 120px;
  text-align: center;
  color: #ccc;
}
.widget_block {
  display: table-cell;
  vertical-align: middle;
}
.top_widgets select{
  background: #323232;
  color:#ccc;
  height: 46px;
  border: none;
  padding: 0 15px;
}
.top_widgets select:focus{
  border: none;
}

._tweet:hover{
  background-color: #00ACED;
}