/* Nav */
.nav {
  height: 92px;
  background-color: #fff;
  justify-content: space-between;
}
.logo {
  width: 150px;
}
.logo a {
  text-align: center;
  display: block;
  font-size: 60px;
  text-decoration: none;
  color: #43CB83;
}
.nav_right {
  align-items: center;
}
.nav_right a {
  color: #333;
  text-align: center;
  text-decoration: none;
  display: block;
  width: 120px;
  font-weight: 500;
  transition: .3s;
}
.nav_right a:hover {
  color: #43CB83;
}
