/* profile */
.profile {
  background-color: #fff;
}
.hero {
  background-image: url(https://image.shutterstock.com/image-photo/businessman-suit-cape-hero-stand-260nw-1035824698.jpg);
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  width: 100%;
  height: 220px;
  margin-bottom: 20px;
}
.avatar {
  width: 150px;
  height: 150px;
  background-image: url(https://images.unsplash.com/photo-1611915387288-fd8d2f5f928b?ixlib=rb-1.2.1&ixid=MnwxMjA3fDB8MHxleHBsb3JlLWZlZWR8MXx8fGVufDB8fHx8&w=1000&q=80);
  background-size: cover;
  margin-right: 10px;
}
.info_block {
  display: flex;
  flex-direction: row;
  margin-bottom: 20px;
}
.info_text h3 {
  margin-top: 0;
}
.info_text h4 {
  margin: 5px 0 5px;
  padding: 0;
}